body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: rgba(0,0,0,0);
}

.Content{
  background-color: rgba(0,0,0,0);
}

.Header{
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 15px;
  padding-bottom: 10px;

  background-image: linear-gradient(rgba(0, 0, 0, .25),rgba(67, 67, 67, .25));

  /*border: 3px solid rgba(255,255,255,.5);*/
}

.NavNames a{
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 15pt;
  font-family: 'Ubuntu', Helvetica, Arial, sans-serif;

  padding:15px;
  padding-right: 20px;


  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  -o-transition: all .5s ease;

  -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(.7,transparent), to (rgba(0,0,0,.1)));

}

.NavNames a:hover{
  opacity: 1;

   /*Reflection*/
  -webkit-box-reflect: below 0px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(.7, transparent), to(rgba(0,0,0,0.4)));

   /*Glow*/
  box-shadow: 0px 0px 20px rgba(255,255,255,0.8);
}

.footer-container {
     text-align: center;
     color: white;
     position: absolute;
     left: 0;
     bottom: 0;
     height: 40px;
     width: 100%;
     overflow: hidden;
     padding-bottom: 15px;
}

.footer-container2 p{
  line-height: 1em;
  font-size: 10pt;
}

.video-background {
    background: #000;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: -99;
    &::after {
        display: block;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background: rgba(0,0,0,0.75)
    }
}

.video-foreground{
  opacity:.20;
}
.video-foreground,
.video-background iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

@media (min-aspect-ratio: 16/9) {
    .video-foreground { height: 300%; top: -100%; }
}
@media (max-aspect-ratio: 16/9) {
    .video-foreground { width: 300%; left: -100%; }
}

